.single__feature span{width:50px;height:50px;border-radius:50%;background:#ff766b;display:flex;align-items:center;justify-content:center;margin-bottom:10px}.single__feature span i{font-size:1.5rem}.choose__content h4{color:#fff;margin-bottom:15px}.single__feature h6{color:#f5b70a;margin-bottom:0}.single__feature p{font-size:.7rem}.feature__icon-two{background:#2ace92 !important}.feature__icon-3{background:#f5b70a !important}.feature__icon-4{background:#7865ff !important}@media only screen and (max-width: 992px){.choose__content h2{font-size:1.5rem}.choose__content h4{font-size:1rem}.choose__content p{font-size:.8rem}.single__feature h6{font-size:.8rem}.single__feature p{font-size:.6rem}}
.filter-btn{border:1px solid #f76e11;padding:7px 25px;background:rgba(0,0,0,0);margin-left:15px;color:#f76e11;border-radius:5px;cursor:pointer}.menu__title{color:#f5b70a}.active__btn{background:#f76e11;color:#fff}@media only screen and (max-width: 992px){.menu__title{font-size:1.4rem}.filter-btn{padding:5px 20px;font-size:.8rem}}@media only screen and (max-width: 767px){.filter-btn{margin-bottom:15px}}
.single__testimonial h6{color:#f5b70a;font-size:1.1rem}.single__testimonial p{font-size:.8rem;color:#f76e11}.single__testimonial{padding:20px}@media only screen and (max-width: 992px){.slider__content h2{font-size:1.5rem;margin-bottom:15px !important}.single__testimonial p{font-size:.7rem}}@media only screen and (max-width: 767px){.slider__wrapper{flex-direction:column}.slider__content{width:100% !important}.slider__img{display:none}}
.app__container{background:#f5b70a;border-radius:10px}.app__img{margin-bottom:-100px}.app__content{padding-top:50px}.app__content h5{color:#0a071a}.app__content h2{color:#0a071a;font-weight:600}.app__content p{color:#fff}.btn__apple{background:#f76e11;border:none;color:#fff;padding:7px 25px;border-radius:5px}.btn__apple a{style:none}.btn__google{background:#fff;color:#0a071a;border:none;padding:7px 25px;border-radius:5px}.btn__google i{color:#0a071a}.btn__apple:hover a{color:#fff}.btn__google:hover a{color:#0a071a}@media only screen and (max-width: 992px){.app__img img{width:100%}.app__content h5{font-size:1rem}.app__content h2{font-size:1.5rem}.app__content p{font-size:.7rem;line-height:25px}.btn__apple,.btn__google{padding:5px 20px;font-size:.8rem}.app__content{padding-bottom:50px}}@media only screen and (max-width: 768px){.app__content{padding:30px}.app__img{margin-bottom:0}}
